@import url(https://fonts.cdnfonts.com/css/futura-pt);
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

:root {
    --cor0: #53DBC9;
    --cor1: #6EA29B;
    --cor2: #368E82;
}

.header {
    font-family: 'Futura PT', sans-serif;                                                
    background-image: linear-gradient(to bottom, #53DBC9, #368E82);                                                
    background-image: linear-gradient(to bottom, var(--cor0), var(--cor2));
    color: #edf9ef;
    padding: .5em;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

@media screen and (max-width: 960px){
    .header {
        flex-direction: row;
        align-items: center;
    }   
}
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
         
.containerMeeting {
    padding: 5px 20px;
    display: flex;
    /* flex-direction: row; */
    justify-content: space-between;
    background-color: #75dbcd;
    min-width: 320px;
    max-width: 1440px;
}

.containerMeeting p {
    width: 80%;
    margin: 0 auto;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif; 
    font-size: 1.1em;
    font-weight: 400;
}

.containerCard {
    width: 25em;
    margin-top: 1.5em;
    /* border: thin solid black; */
    border-radius: 3px;
    display: flex;
}

.containerCard img {
    width: 45%;
    border-radius: 50%;
    margin-right: 1em;
}

.firstButton {
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: 400;
    font-size: large;
}

@media screen and (max-width: 960px) {
    .containerMeeting {
        flex-direction: column;
        align-items: center;
    }   
}

