* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
} /* https://www.cdnfonts.com/futura-pt.font */ 

.containerHeader {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    background-color: #75dbcd;
    min-width: 320px;
    max-width: 1440px;
    margin: auto;
}

.containerHeader h1 {
    font-family: 'Futura PT', sans-serif;   
    font-size: 2.5em;                                             
    font-weight: 600;
    /* margin-top: 1em; */
    text-shadow: 1px 3px 3px rgb(0 0 0 / 23%);
}

.containerHeader h2 {
    font-family: 'Futura PT', sans-serif;                                                
    font-size: 1.999em;
    font-weight: 300;  
    text-shadow: 0px 3px 4px rgb(0 0 0 / 23%);

}

.buttonCTA {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

.buttonCTA button {
    font-size: 1.5em;
    padding: .5em 2em;
}

@media screen and (max-width: 1230px) {
    .containerHeader {
        text-align: center;
    }   
}



